import { createRouter, createWebHashHistory } from 'vue-router'
import ExpView01 from '../views/ExpView01.vue'

const routes = [
  {
    path: '/Exp/01',
    name: 'ExpView01',
    component: ExpView01
  },
  {
    path: '/Exp/02',
    name: 'ExpView02',
    component: () => import('../views/ExpView02.vue')
  },
  {
    path: '/Exp/03',
    name: 'ExpView03',
    component: () => import('../views/ExpView03.vue')
  },
  {
    path: '/Exp/04',
    name: 'ExpView04',
    component: () => import('../views/ExpView04.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
