<template>
    <div class="ExpView01"></div>
</template>

<script>
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'

export default {
  name: 'ExpView01',
  data () {
    return {}
  },
  methods: {
    main () {
      this.canvas = document.querySelector('.ExpView01')
      this.renderer = new THREE.WebGLRenderer({ antialias: true })
      this.renderer.setSize(this.canvas.clientWidth, this.canvas.clientHeight)
      this.renderer.shadowMap.enabled = true
      this.canvas.appendChild(this.renderer.domElement)

      this.scene = new THREE.Scene()

      const fov = 75
      const width = this.canvas.clientWidth
      const height = this.canvas.clientHeight
      const near = 0.01
      const far = 1000
      this.camera = new THREE.PerspectiveCamera(fov, width / height, near, far)
      this.camera.position.set(0, 5, 10)
      this.scene.add(this.camera)

      this.control = new OrbitControls(this.camera, this.renderer.domElement)

      const planeGeo = new THREE.PlaneGeometry(10, 10)
      const planeMate = new THREE.MeshStandardMaterial({
        color: '#ffffff',
        roughness: 0.5,
        metalness: 0.3,
        side: THREE.DoubleSide
      })
      const plane = new THREE.Mesh(planeGeo, planeMate)
      plane.rotation.x = THREE.MathUtils.degToRad(-90)
      plane.receiveShadow = true
      this.scene.add(plane)

      const sphereGeo = new THREE.SphereGeometry(1, 64, 64, 0, Math.PI)
      const sphereMate = new THREE.MeshStandardMaterial({
        color: '#e68405',
        roughness: 0.1,
        metalness: 0.2
      })
      const sphere = new THREE.Mesh(sphereGeo, sphereMate)
      sphere.rotation.x = THREE.MathUtils.degToRad(-90)
      sphere.castShadow = true
      sphere.receiveShadow = true
      this.scene.add(sphere)

      const torusGeo = new THREE.TorusGeometry(0.4, 0.1, 64, 64)
      const torusMate = new THREE.MeshStandardMaterial({
        color: '#ff9999',
        roughness: 0.5,
        metalness: 0.8
      })
      for (let i = 0; i < 8; i++) {
        const torusPivot = new THREE.Object3D()
        const torus = new THREE.Mesh(torusGeo, torusMate)
        torusPivot.rotation.y = THREE.MathUtils.degToRad(45 * i)
        torus.position.set(3.5, 0.5, 0)
        torusPivot.add(torus)
        torus.castShadow = true
        torus.receiveShadow = true
        this.scene.add(torusPivot)
      }

      const smallSphereGeo = new THREE.SphereGeometry(0.3, 64, 64)
      const smallSphereMate = new THREE.MeshStandardMaterial({
        color: '#ffffff',
        roughness: 0.5,
        metalness: 0.3
      })
      const smallSpherePivot = new THREE.Object3D()
      const smallSphere = new THREE.Mesh(smallSphereGeo, smallSphereMate)
      smallSphere.position.set(3.5, 0.5, 0)
      smallSpherePivot.add(smallSphere)
      smallSphere.castShadow = true
      smallSphere.receiveShadow = true
      smallSpherePivot.name = 'smallPivot'
      this.scene.add(smallSpherePivot)

      this.light = new THREE.DirectionalLight({ color: 0xffffff, intensity: 0.5 })
      this.light.position.set(0, 5, 0)
      this.light.target.position.set(0, 0, 0)
      this.scene.add(this.light)
      this.scene.add(this.light.target)
      this.light.castShadow = true
      this.light.shadow.mapSize.width = this.light.shadow.mapSize.height = 2048
      this.light.shadow.camera.scale.set(1.3, 1.3, 1.3)
    },
    render (time) {
      time *= 0.001
      this.renderer.render(this.scene, this.camera)
      this.control.update()
      requestAnimationFrame(this.render)
      this.reSize()

      const smallPivot = this.scene.getObjectByName('smallPivot')
      if (smallPivot) {
        smallPivot.rotation.y = THREE.MathUtils.degToRad(time * 50)
        const chasePivot = smallPivot.children[0]
        chasePivot.getWorldPosition(this.light.target.position)
      }
    },
    reSize () {
      const canvas = this.canvas
      this.renderer.setSize(canvas.clientWidth, canvas.clientHeight)
      this.camera.aspect = canvas.clientWidth / canvas.clientHeight
      this.camera.updateProjectionMatrix()
    }
  },
  mounted () {
    this.main()
    this.render()
  }
}
</script>

<style scoped lang="scss">
  .ExpView01{
    width:100%;
    height: 100%;
  }
</style>
