<template>
    <div class="background"></div>
</template>

<script>
import * as THREE from 'three'
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader'
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry'

export default {
  name: 'BackGround',
  data () {
    return {}
  },
  methods: {
    init () {
      const backCanvas = document.querySelector('.background')
      this.renderer = new THREE.WebGLRenderer({ antialias: true })
      this.renderer.setSize(backCanvas.clientWidth, backCanvas.clientHeight)
      this.renderer.setPixelRatio(window.devicePixelRatio)
      this.renderer.shadowMap.enabled = true
      backCanvas.appendChild(this.renderer.domElement)

      this.scene = new THREE.Scene()

      const aspect = backCanvas.clientWidth / backCanvas.clientHeight
      this.camera = new THREE.PerspectiveCamera(75, aspect, 0.1, 1000)
      this.camera.position.set(0, 0, 100)
      this.scene.add(this.camera)

      this.light = new THREE.SpotLight(0xffffff, 1, 0)
      this.light.position.set(0, 0, 100)
      this.light.target.position.set(0, 0, 0)
      this.light.angle = THREE.MathUtils.degToRad(20)
      this.scene.add(this.light.target)
      this.scene.add(this.light)

      const smallSphereGeo = new THREE.SphereGeometry(5, 64, 64)
      const smallSphereMate = new THREE.MeshStandardMaterial({
        color: '#ffffff',
        roughness: 0.5,
        metalness: 0.3
      })
      const smallSpherePivot = new THREE.Object3D()
      const smallSphere = new THREE.Mesh(smallSphereGeo, smallSphereMate)
      smallSphere.position.set(-50, 0, 0)
      smallSpherePivot.add(smallSphere)
      smallSphere.castShadow = true
      smallSphere.receiveShadow = true
      smallSphere.visible = false
      smallSpherePivot.name = 'smallPivot'
      this.scene.add(smallSpherePivot)

      const loader = new FontLoader()
      loader.load('data/fonts/Millimetre-Extrablack_Regular.json', (font) => {
        const geometry = new TextGeometry('WEB\nGraphic\nExp', {
          font: font,
          size: 30,
          height: 1
        })

        geometry.center()

        const material = new THREE.MeshStandardMaterial({
          color: 0xffffff
        })

        this.mesh = new THREE.Mesh(geometry, material)
        this.mesh.position.set(-15, 0, 0)
        this.mesh.castShadow = true
        this.mesh.receiveShadow = true
        this.scene.add(this.mesh)
      })
      this.resize()
    },
    animate (time) {
      time *= 0.001
      requestAnimationFrame(this.animate)
      this.renderer.render(this.scene, this.camera)
      this.resize()
      const smallPivot = this.scene.getObjectByName('smallPivot')
      if (smallPivot) {
        smallPivot.rotation.z = THREE.MathUtils.degToRad(time * 40)
        const chasePivot = smallPivot.children[0]
        chasePivot.getWorldPosition(this.light.target.position)
      }
    },
    resize () {
      const container = document.querySelector('.background')
      const width = container.clientWidth
      const height = container.clientHeight
      this.camera.aspect = width / height
      this.camera.updateProjectionMatrix()
      this.renderer.setSize(width, height)
    }
  },
  mounted () {
    this.init()
    this.animate()
  }
}
</script>

